.f{
    align-items: center;
    background-color: #dc143c;
    padding: 10px 0px;
    width: 100%;
    position: relative;
    margin-top: -80px;
    height: 80px;
    clear: both;
}
.f-copy{
    color: #fff;
    display: flex;
    text-align: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 9px;
}
.f-copy span{
    font-size: 15px;
}
.f-copy a{
    color: black;
    text-decoration: none;
}
.icons{
    margin-top: 10px;
    align-items: center;
    text-align: center;
}
.icons a{
    font-size: 18px;
    color: #fff;
    margin: 0 13px;
    cursor: pointer;
    padding: 18px 0;
}
.icons a:hover{
    color: black;
}
@media screen and (max-width: 480px) {
    .f{
        width: 100%;
        /* margin-top: 15px; */
    }
    .f-copy span{
        font-size: 15px;
    }
}
@media screen and (max-width: 1284px) {
    .f{
        width: 100%;
    }
}