.c{
    height: 100vh;
    position: relative;
    margin-bottom: 70px;
}
.c-bg{
    width: 20px;
    height: 100%;
    background-color: #dc143c ;
    position: absolute;
}
.c-wrapper{
    padding: 50px;
    display: flex;
}
.c-left{
    flex: 1;
}
.c-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.c-title{
    font-size: 60px;
    width: 80%;
}
.c-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
}
.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}
.c-desc{
    font-weight: 200;
}
form{
    margin-top: 20px;
}
input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid #dc143c;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    border-radius: 8px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    border: none;
    border: 1px solid #dc143c;
    border-radius: 8px;
}
button{
    border: 2px solid #dc143c;
    border-radius: 20px;
    padding: 15px;
    background-color: #dc143c;
    color: #fff;
    font-size: 17px;
    font-weight: 500;
    cursor: pointer;
    width: 40%;
}
button:hover{
    color: #dc143c;
    background: transparent;
}



@media screen and (max-width: 480px) {
    /* .c{
        height: 90vh;
    } */
    .c-wrapper {
      flex-direction: column;
      padding: 0px 50px;
    }
  
    .c-title {
      font-size: 30px;
    }
  
    .c-info-item {
      margin: 20px 0px;
      width: 100%;
    }
  
    .c-desc {
      display: none;
    }
  
    form {
      margin-top: 0px;
      display: flex;
      flex-wrap: wrap;
    }
  
    input {
      width: 35%;
      height: 40px;
      margin: 10px;
      margin-left: 0;
    }
  
    button {
      margin-top: 10px;
    }
}  
@media screen and (max-width: 280px) {
    .c-title {
        font-size: 25px;
    }
}
